.canvasContainer{
  height:100%;
  width:100%;
}
.toolBar{
  height:25px;
  padding:10px;
  border-radius: 10px;
  background-color:#fff;
  box-shadow: 1px 1px 6px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
}

.toolBarWrapper{
  padding: 10px 0 0 0;
  position:absolute;
  left:30%;
  z-index:10;
}

.dndflow {
  flex-direction: row;
  display: flex;
  height: 100%;
}

.dndflow aside {
  /* border-right: 1px solid #eee; */
  box-shadow: 1px 1px 2px 0 rgba(0,0,0,0.1);
  z-index:7;
  transition: 0.3s;
  padding: 15px 10px;
  font-size: 12px;
  background: #ffffff;
}

.dndflow aside > * {
  margin-bottom: 10px;
  cursor: grab;
}

.dndflow aside .description {
  margin-bottom: 10px;
}

.dndflow .reactflow-wrapper {
  flex-grow: 1;
  height: 100%;
}

.sidebar {
  overflow-y:scroll;
}
@media screen and (min-width: 768px) {
  .dndflow {
    flex-direction: row;
  }

  .dndflow aside {
    width: 20%;
    max-width: 180px;
  }
}

