.dashboardPage{
    background-color: rgb(212, 212, 212);
}

.datasetSection{
    position:fixed;
    top:47px;
    padding:20px;
    min-width: 250px;
    width: 30vw;
    height:calc(100vh - 47px);
    /* box-shadow: 1px 1px 6px 0 rgba(0,0,0,0.2); */
    transition: 0.3s;
    /* background-color: rgb(250, 250, 250); */
}

.topSection{
    background-color: #fff;
    border-radius:10px;
    padding: 10px 10px 10px 10px;
    box-shadow: 1px 1px 4px 0 rgba(0,0,0,0.2);
    height:125px;
}

.dataAssetTitle{
    height:20px;
    font-size:20px;
    padding-left:10;
    border-bottom:0.5px solid black;
}

.bottomSection{
    overflow-y:scroll;
    height: calc(100vh - 280px);
    padding: 0 10px 0 10px;
}

.dataAsset{
    background-color: #fff;
    padding:10px;
    box-shadow: 1px 1px 2px 0 rgba(0,0,0,0.1);
    border-radius: 2px;
}

.dataObject{
    border-left:2px solid rgb(255, 0, 85);
}

.dataSet{
    border-left:2px solid rgb(0, 98, 255);
}

.trainingHistoryContainer{
    float:right;
    height:calc(100vh - 47px);
    width: calc(100vw - max(30vw, 250px) - 40px);
}

.trainingHistoryWrapper{
    padding:20px 10px 10px 10px;
}

.canvasWrapper{
    width:100%;
    background-color: blue;
}