.topbar{
    height:47px;
    width:100vw;
    min-width:950px;
    background-color:white;
    color:rgb(85, 78, 78);
    font-size:20px;
    z-index:5;
    box-shadow: 1px 1px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}


.brand{
    color:rgb(59, 59, 59);
    font-size:40px;
    padding:0 0 0 10px;
}

.title{
    position:absolute;
    left:10px;
    top:0px;
}

.navbarContainer{
    position:absolute;
    left:100px;
    top:0px;
}
.navbar{
    padding:20px 0 0 0;
}

.navLink{
    color:black;
}